import React, {useState} from "react";
import './newsLine.module.css';
import classes from './newsLine.module.css';
import {v4} from "uuid";
import {ModalCustom} from "./ModalCustom";
import {imagesNewFive, imagesNewFour, imagesNewOne, imagesNewSix, imagesNewThree, imagesNewTwo} from "./images";


export interface IData {
    id: string
    title: string
    images: IImages[]
    description: IDescription
    splashPictureUrl: string
}

export interface IDescription {
    title: string
    description: string
    date: string
}

export interface IImages {
    id: string,
    imageUrl: string
}

export interface IModal {
    images: IImages[]
    description: IDescription
}


export const NewsLine = () => {
    const [isModalOpen, setIsModalOpen] = useState(false);
    const [objectModal, setObjectModal] = useState<IData | null>(null);

    const showModal = (object: IData) => {
        setIsModalOpen(true);
        setObjectModal(object)
    };

    const handleOk = () => {
        setIsModalOpen(false);
    };

    const handleCancel = () => {
        setIsModalOpen(false);
    };

    return (
        <div className={classes.wrapGrid}>
            <>
                {data.map((item) =>
                        <div key={item.id} className={classes.wrapCard} onClick={() => showModal(item)}>
                            <div  className={classes.imageBlock} >
                                <div className={classes.imageBlockHover}></div>
                                <img className={classes.wrapCardImage}
                                     src={item.splashPictureUrl}
                                     alt="Picture"
                                />
                            </div>
                        </div>
                )}
                {objectModal &&
                <ModalCustom modal={objectModal} handleOk={handleOk} handleCancel={handleCancel}
                             isModalOpen={isModalOpen}/>
                }
            </>
        </div>
    );
}


